    var that;
    import numberBox from "../../components/number-box.vue";
    import timestampToTime from "../../common/timestampToTime.js";
    import gaodeDt from "../../common/gaode_dt.js";
    import uniNoticeBar from '@/components/uni-notice-bar/uni-notice-bar.vue';
	export default {
        components:{
            numberBox,uniNoticeBar
        },
		data() {
			return {
                uName: '', //发起者名字
				roomName: '', //课堂名称
                times: '', //签到时间
                signInWay: '', //签到方式
                signInWayTxt:'',//签到方式文字
                signCode: '', //签到码
                totalTime: 0, //总时间，单位秒
                recordingTime: 0, //记录时间变量
                currentTime: 0, //显示时间变量
                timeOut: '',
                openMode: '' ,//打开方式（create是创建者打开，join是参与者打开）
                signId:'' ,//签到Id
                codeSign:'' ,//连续的签到码
                inputCode:'' ,//输入的数字
                signStatus:0 ,//签到状态（0表示待签到、1表示已签到、2表示签到结束、3表示时间结束且已签到、4表示不在范围内）
                
                dizhi:'',//地址
                latitude: 0,//纬度
                longitude: 0,//经度
                rangeTxt:'',//范围地址信息
                covers: [{//标记
                    latitude: 0,
                    longitude: 0,
                    iconPath: './static/images/inc/location.png'
                }]
			}
		},
        onLoad(option) {
            that = this;
            uni.showLoading({
                title: '加载中...',
            });
            this.signId = option.signId;
            this.openMode = option.openMode;
            setTimeout((e)=> {
                this.getSignInfo(this.signId);
            },1000)
        },
		methods: {
            getCode(val) {
                this.inputCode = val;
            },
            //设置计时器
            checking() {
                //把显示时间设为总时间
                // this.signStatus = 0;
                this.currentTime = this.totalTime;
                //执行倒计时
                this.checkingTime();
            },
            //计时器开始计时
            checkingTime() {
                clearTimeout(this.timeOut);
                //判断显示时间是否已到0，判断记录时间是否已到总时间
                if (this.currentTime > 0 && this.recordingTime <= this.totalTime) {
                    //记录时间增加 1
                    this.recordingTime = this.recordingTime + 1;
                    //显示时间，用总时间 - 记录时间
                    this.currentTime = this.totalTime - this.recordingTime;
                    //1秒钟后，再次执行本方法
                    this.timeOut = setTimeout(() => {
                        //定时器内，this指向外部，找不到vue的方法，所以，需要用that变量。
                        that.checkingTime();
                    }, 1000);
                } else {
                    //时间已完成，还原相关变量
                    this.recordingTime = 0; //记录时间为0
                    // 判断时间结束前时候完成了签到,完成就显示已签到,未完成就显示签到结束
                    if (this.signStatus == 1) {
                        this.signStatus = 3
                    } else {
                        this.signStatus = 2
                    }
                }
            },
            //开始获取签到信息
			getSignInfo(signId) {
                let userId = uni.getStorageSync('USERID');
                uni.request({
                    url: that.apiServer + 'sign&m=getSignInfo',
                    method: 'POST',
                    header : {'content-type':'application/x-www-form-urlencoded'},
                    data: {
                        signId,userId
                    },
                    success: res => {
                        res = res.data;
                        //课堂名称
                        this.roomName = res.data.roomName;
                        this.uName = res.data.uName;
                        this.signStatus = res.data.signStatus;
                        //拼接时间
                        let dates = timestampToTime.timestampToTime(res.data.beginTime, 2);
                        let times = timestampToTime.timestampToTime(res.data.beginTime, 3);
                        let signTime = timestampToTime.timestampToTime(res.data.signTime, 3);
                        this.times = dates + '' + times + '-' + signTime;
                        //倒计时时间
                        this.totalTime = parseInt(res.data.signTime - Math.floor(new Date / 1000));
                        this.checking();
                        uni.hideLoading();
                        //签到方式
                        switch (res.data.signInWay){
                            case 'code':
                                this.signInWay = 'code';
                                this.signInWayTxt = '签到码'
                                // 正则处理签到码
                                let code = res.data.signCode;
                                this.codeSign = code;
                                let regexp=/\d{1}/g, match;
                                let signCode = new Array();
                                while((match = regexp.exec(code))!=null) {
                                    signCode.push(match[0]);
                                }
                                this.signCode = signCode;
                                break;
                            case 'gps':
                                this.signInWay = 'gps';
                                this.signInWayTxt = 'GPS'
                                this.latitude = res.data.latitude;
                                this.longitude = res.data.longitude;
                                //获取原来签到的目的地
                                var point = new plus.maps.Point(res.data.longitude, res.data.latitude);
                                plus.maps.Map.reverseGeocode(
                                    point,
                                    {},
                                    function(event) {
                                        var address = event.address; // 转换后的地理位置
                                        var point = event.coord; // 转换后的坐标信息
                                        var coordType = event.coordType; // 转换后的坐标系类型
                                        that.dizhi = '目标地点：' + address;
                                    },
                                    
                                );
                                this.covers[0].latitude = res.data.latitude;
                                this.covers[0].longitude = res.data.longitude;
                                // 刷新签到范围
                                this.refreshRange();
                                break;
                            case 'face':
                                this.signInWay = 'face';
                                this.signInWayTxt = '人脸'
                                break;
                            default:
                                break;
                        }
                    },
                });
            },
            //点击签到
            toSignIn(signId) {
                var signInWay = this.signInWay;
                switch (signInWay){
                    case 'code':
                        if (this.inputCode === this.codeSign) {
                            this.signOrder(signId);
                        } else {
                            uni.showToast({title:'签到码错误',icon:'none'});
                            return;
                        }
                        break;
                    case 'gps':
                        this.signOrder(signId);
                        break;
                    case 'face':
                        break;
                    default:
                        break;
                }
            },
            //签到提交
            signOrder(signId) {
                let userId = uni.getStorageSync('USERID');
                uni.request({
                    url: that.apiServer + 'sign&m=toSignIn',
                    method: 'POST',
                    header: {'content-type': 'application/x-www-form-urlencoded'},
                    data: {signId,userId},
                    success: res => {
                        res = res.data;
                        if (res.status == 'error') {
                            uni.showToast({title:res.data,icon:'none'});
                        } else if (res.status == 'ok') {
                            if (res.data == '签到成功') {
                                uni.showToast({title:res.data,icon:'none'});
                                this.signStatus = 1;
                            }
                        }
                    }
                });
            },
            //查看签到情况
            viewSign(signId) {
                uni.navigateTo({
                    url:'../viewSign/viewSign?signId='+signId
                })
            },
            // 计算两个经纬度之间的距离
            computedDt(lon,lat) {
                var start = new gaodeDt.LngLat(this.longitude,this.latitude);
                var end = new gaodeDt.LngLat(lon,lat);
                let result = gaodeDt.calculateLineDistance(start, end)
                return result;
            },
            // 刷新签到范围
            refreshRange() {
                //获取当前坐标
                uni.showLoading({
                    title:'正在计算距离中...'
                })
                setTimeout(e => {
                    uni.getLocation({
                        type: 'gcj02',
                        success: (res) => {
                            //计算当前距离
                            let result = this.computedDt(res.longitude,res.latitude);
                            if (result <= 50) {
                                this.rangeTxt = '当前已进入签到范围内(50米)';
                            } else {
                                this.rangeTxt = '当前未进入签到范围';
                                this.signStatus = 4;
                            }
                            uni.hideLoading()
                        }
                    });
                }, 500);
            }
		}
	}
